import * as postRobot from '../post-robot-wrapper';
import {getComponentWindow} from './createComponent';
import {config} from '../app/components/config';
import {isRedirectDisabled} from './create-dropin';

export function sendRedirectSourceData(controllerId, sourceId, clientSecret, url, paymentMethodType) {
  const controllerWindow = getComponentWindow(controllerId);

  return postRobot.send(controllerWindow, 'handleDropInRedirect', {sourceId: sourceId, clientSecret: clientSecret, url: url, paymentMethodType: paymentMethodType}).catch(() => {
    throw new Error('Failed to store data');
  });
}

export function registerRedirectOnComplete(controller, options) {

  const controllerWindow = getComponentWindow(controller.id);

  postRobot.on('redirectComplete', { window: controllerWindow, domain: config.domain }, (event)  => {
    const {response, action, paymentMethodType} = event.data;
    if (action === 'return') {
      runClientProvidedCompleteEvents(options, response);
    } else {
      runClientProvidedCancelEvent(options, paymentMethodType);
    }
  });
}

export function isReadyForStorage(sourceData) {
  const hasSignedMandate = typeof sourceData.source.mandate !== 'undefined' && sourceData.source.mandate.hasOwnProperty('terms') && sourceData.source.mandate.hasOwnProperty('signedTime');
  return typeof sourceData.source !== 'undefined' && sourceData.source.state === 'chargeable' && hasSignedMandate === true;
}

export function runClientProvidedCompleteEvents(options, sourceData) {

  const isSuccess = typeof sourceData.source !== 'undefined' && sourceData.source !== null;
  const redirectDisabled = isRedirectDisabled(options);

  if (options.onSuccess && isSuccess && (sourceData.source.state === 'pending_redirect' || sourceData.source.state === 'cancelled') && !redirectDisabled) {
    runClientProvidedCancelEvent(options, sourceData.source.type);
  } else if (options.onSuccess && isSuccess) {
    options.onSuccess({
      source: sourceData.source,
      readyForStorage: isReadyForStorage(sourceData)
    });
  } else if (options.onError && !isSuccess) {
    options.onError(sourceData.error);
  }
}

export function createCancelEvent(paymentMethodType) {
  return {
    paymentMethod: paymentMethodType
  };
}

export function runClientProvidedCancelEvent(options, paymentMethodType) {
  if (options.onCancel) {
    options.onCancel(createCancelEvent(paymentMethodType));
  }
}
